











import Vue from 'vue';
import { Component, Prop } from 'vue-property-decorator';

@Component
export default class RecordListItem extends Vue {
  @Prop({default: 0}) readonly type!: number; // 0 1 2 3 
  typeClassName = {
    '0': 'regular',
    '1': 'third',
    '2': 'second',
    '3': 'first'
  };
}
